.MainDiv {
    display: flex;
    justify-content: center;
}

.MyIcon {
    display: flex;
    color: white;
    margin: 30px 70px;
    text-shadow: 2px 2px 0px rgb(54, 54, 54);
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.MyIcon:hover {
    text-shadow: 3.5px 2px 0px rgb(54, 54, 54);
    color: rgb(31, 177, 97);
    cursor: pointer;
}

@media (max-width: 768px) {
    .MyIcon:hover {
        font-size: inherit;
    }
    .MyIcon {
        margin: 40px 70px;
    }
    .MainDiv {
        flex-wrap: wrap;
    }
}

.MyIcon:active {
    color: rgb(82, 255, 93);
    transition: .1s;
    animation: none;
}

.MyIcon:last-child:hover{
    color: blueviolet;
    animation: none;
}

.MyIcon:last-child:active{
    color: rgb(173, 85, 255);
}

.MyIcon:nth-last-child(2):hover {
    color: blueviolet;
    animation: none;
}

.MyIcon:nth-last-child(2):active {
    color: rgb(189, 119, 255);
}
