.MessageBox {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    pointer-events: none;

    position: absolute;
    margin: 10px 10px 0 0;
    
    width: 300px;
    height: 80px;
    top: 0;
    right: 0;

    background: rgba(222, 222, 222, 0.324);

    border: 1px solid transparent;
    border-radius: .6em;
}

.Avatar {
    margin-left: 8px;
    width: 60px;
    height: 60px;
    pointer-events: none;

    background: url("../content/avatars/1.jpg") no-repeat;
    background-size: cover;

    border: 1px solid transparent;
    border-radius: 5.6em;
}

.Icon {
    display: flex;
    justify-content: center;
    align-items: center;
    pointer-events: none;

    margin: 40px 0 0 40px;
    width: 20px;
    height: 20px;

    background: rgba(24, 24, 24, 0.646);

    border: 1px solid transparent;
    border-radius: 40px;
}


.Name {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 16px;
    font-weight: 600;

    margin: 0 0 45px 20px;
}
